.no-display h3 {
  font-size: 1rem;
  color: #fff;
  border: 1px solid red;
}
.single-movie-section {
  /* display: flex; */
  /* flex-direction: column; */
  padding: 0.25rem;
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 0.25rem;
  /* max-height: 100%; */
  /* height: 100%; */
  /* overflow: auto; */
  margin: auto;
  border: 1px solid red;
  /**/
  /* max-height: 800px; */
  overflow: hidden;
  overflow-y: auto;
}
.single-container {
  display: flex;
  flex-direction: column;
  /* overflow-y: auto; */
}
.single-title {
  flex: 0 0 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 0.25rem;
  text-align: center;
  color: #fff;
  /* border: 1px solid red; */
}
.single-movie {
  flex: 1;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 1fr;
  gap: 0.25rem;
  padding: 0.25rem;
  /* overflow: hidden; */
  overflow-y: auto;
  overflow: hidden;
  /* border: 1px solid red; */
}

.single-center {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  /* border: 1px solid blue; */
}
.single-img {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: fill;
}
.movie-info {
  display: flex;
  flex-direction: column;
  padding: 0.25rem;
  overflow-y: auto;
  overflow: auto;
  color: #fff;
  /* border: 1px solid blue; */
}
.movie-info p {
  display: flex;
  /* flex-direction: column; */
  flex-direction: row;
  flex-wrap: wrap;
  margin-bottom: 0.25rem;
  padding: 0.25rem;
  font-weight: bold;
  letter-spacing: 0.1rem;
  text-transform: capitalize;

  /* border: 1px solid red; */
}

.movie-info span {
  font-weight: 100;
  letter-spacing: normal;
  /* text-indent: 0.2rem; */
  margin: 0 0.25rem;
}
@media screen and (max-width: 479px) {
  .single-movie {
    grid-template-columns: repeat(2, 1fr);
  }
  .single-title {
    flex: 0 0 90px;
    font-size: 1.4rem;
  }
  .single-img {
    max-width: 400px;
  }

  .movie-info p {
    flex-direction: column;
    flex-wrap: wrap;
    font-size: 1.1rem;
  }

  .movie-info span {
    font-size: 1rem;
  }
}
@media screen and (min-width: 480px) {
  .single-title {
    flex: 0 0 95px;
    font-size: 1.45rem;
  }

  .movie-info p {
    flex-direction: row;
    flex-wrap: wrap;
    font-size: 1.15rem;
  }
  .movie-info span {
    font-size: 1.05rem;
  }
}
@media screen and (min-width: 780px) {
  .single-title {
    flex: 0 0 100px;
    font-size: 1.5rem;
  }

  .movie-info p {
    font-size: 1.2rem;
  }
  .movie-info span {
    font-size: 1.1rem;
  }
}
@media screen and (min-width: 1024px) {
  .single-title {
    flex: 0 0 110px;
    font-size: 1.6rem;
  }

  .movie-info p {
    font-size: 1.3rem;
  }
  .movie-info span {
    font-size: 1.2rem;
  }
}
@media screen and (min-width: 1280px) {
  .single-title {
    flex: 0 0 120px;
    font-size: 1.7rem;
  }

  .movie-info p {
    font-size: 1.4rem;
  }
  .movie-info span {
    font-size: 1.3rem;
  }
}
@media screen and (min-width: 1440px) {
  .single-title {
    flex: 0 0 130px;
    font-size: 1.8rem;
  }

  .movie-info p {
    font-size: 1.5rem;
  }
  .movie-info span {
    font-size: 1.4rem;
  }
}
